<template>
  <div class="template-container">
    <div class="template-container-title">新建账号</div>

    <el-form ref="form" :model="form" :inline="true" label-width="100px">
      <!--广告组 begin -->
      <div class="temp-item-block">
        <div class="temp-item-main">
          <el-form-item label="账号名称">
            <el-input clearable v-model="form.account_name"></el-input>
          </el-form-item>
          <div>
            <el-form-item label="QQ">
              <el-input clearable v-model="form.qq"></el-input>
            </el-form-item>
          </div>
          <div>
            <el-form-item label="广点通账户ID">
              <el-input clearable v-model="form.account_id"></el-input>
            </el-form-item>
          </div>
        </div>

      </div>
      <!--广告组 end -->

      <el-form-item>
        <el-button type="primary" :loading="submitLoading" @click="onSubmit">立即创建</el-button>
        <el-button @click="onCancel">取消</el-button>
      </el-form-item>
    </el-form>
  </div>

</template>

<script>
export default {
  created: function() {},
  data() {
    return {
      submitLoading: false, // 禁止重复提交 -- loading 状态
      form: {
        account_id: '',
        account_name: '',
        qq: ''
      }
    }
  },
  computed: {},
  mounted() {
    //console.log(this.listData)
  },
  methods: {
    onCancel() {
      this.$router.push('/setting/accountmanage')
    },
    onSubmit() {
      var params = this.form
      var that = this
      that.submitLoading = true
      this.$axios
        .get('/Admin/Marketing_Account/add', {
          params: params
        })
        .then(function(response) {
          var respData = response.data
          that.submitLoading = false
          if (respData.code == 0) {
            that.$message({
              type: 'success',
              message: respData.msg
            })
            setTimeout(function() {
              that.$router.push('/setting/accountmanage')
            }, 500)
          }else{
            that.$message.error(respData.msg)
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
          that.submitLoading = false
          console.log(err)
        })
    }
  }
}
</script>


<style scoped>
.temp-radio-item {
  width: 100%;
  margin: 0 0 20px 0;
}
.temp-radio-remark {
  display: inline-block;
  color: #999;
  font-size: 14px;
  margin-left: 100px;
}

.radio-inlineblock {
  display: inline-block;
  margin-left: 20px;
}
</style>



